module.exports = ({ config, optionsBar, stores, util }) => () => {
    const $optionsBarContainer = document.createElement('div');
    $optionsBarContainer.className = 'options-bar-container';

    const $optionsBar = document.createElement('div');
    $optionsBar.className = 'options-bar';

    $optionsBarContainer.append($optionsBar);

    const visibilityToggle = util.visibilityToggle($optionsBar);
    stores.tags.onInsert(visibilityToggle.show);

    const groups = util.parseLayout(config.options.layout).map(group => {
        const options = group.map(name => optionsBar.options[name]());
        const $group = document.createElement('div');
        $group.className = 'option-group';
        $group.append(...options);
        return $group;
    });

    $optionsBar.append(...groups);

    return $optionsBarContainer;
};
