module.exports = ({ services, stores, util }) => async tagData => {
    const pipeline = Object.values(services.tagDataPipeline);
    const tag = await util.pipeAsync(pipeline, tagData);
    return stores.tags.insert(tag, tagId => {
        const { onChange } = stores.tags.id(tagId);
        onChange('name', services.tags.propagateTagName(tagId)).invoke();
        onChange('roleId', services.tags.propagateRoleId(tagId)).invoke();
        services.tags.adjustInstanceCounts()();
    });
};
