module.exports = ({ gravatarImporter }) => ({ done }) => {
    const $gravatar = document.createElement('div');
    $gravatar.title = 'Gravatar';
    $gravatar.className = 'gravatar';

    $gravatar.append(
        gravatarImporter.freetext(), 
        gravatarImporter.fallbacks(), 
        gravatarImporter.actions({ done })
    );

    return $gravatar;
};
