module.exports = ({ components, controlPanel }) => () => {
    const $controlPanel = document.createElement('div');
    $controlPanel.className = 'control-panel';

    $controlPanel.append(
        controlPanel.imageUploadOptions(),
        controlPanel.briefUsageNotice(),
        controlPanel.roleList(),
        components.optionsBar(),
        controlPanel.multiplesNotice()
    );

    return $controlPanel;
};
