module.exports = ({ services, stores }) => (tagId, dataExpression) => {
    const { name, roleName } = services.tags.parseExpression(dataExpression);

    stores.tags.setState(tagId, { name });

    if (roleName) {
        const roleId = services.roles.findOrInsertRoleWithName(roleName);
        stores.tags.setState(tagId, { roleId });
    }
};
