const compose = require('module-composer');
const lib = require('./lib');

module.exports = ({ config, io }) => {
    const state = {};
    const util = compose(lib.util, { io });
    const stores = compose(lib.stores, { util, state });
    const settings = lib.settings({ config, stores });
    const services = compose(lib.services, { util, io, config, stores, settings });
    const elements = compose(lib.elements, { util });
    const components = compose.collapse(lib.components, { util, io, config, stores, settings, services, elements });

    services.system.initialise();
    
    const context = {
        state,
        io,
        util,
        config,
        stores,
        settings,
        services,
        elements,
        components
    };

    return { context, ...context };
};
