module.exports = ({ settings }) => shape => {
    const $shape = document.createElement('span');
    $shape.classList.add('shape-option', shape);
    $shape.title = `Change shape to ${shape}`;

    const toggleSelected = ({ shape: selectedShape }) => {
        $shape.classList.toggle('selected', shape === selectedShape);
    };

    $shape.addEventListener('click', () => {
        settings.options.setState({ shape });
    });

    settings.options.onChange('shape', toggleSelected).invoke();

    return $shape;
};
