module.exports = ({ elements }) => done => {
    const $content = document.createElement('div');
    $content.className = 'welcome';

    const $title = document.createElement('h1');
    $title.className = 'welcome-title';
    $title.textContent = 'Welcome to Agile Avatars';
    $content.append($title);

    const $image = document.createElement('img');
    $image.src = 'img/sample.png';
    $image.width = 800;
    $content.append($image);

    const $actions = document.createElement('div');
    $actions.className = 'actions';
    $content.append($actions);

    const $continue = document.createElement('button');
    $continue.textContent = 'Continue';
    $actions.append($continue);

    $continue.addEventListener('click', () => {
        done();
    });

    return elements.modal({
        content: $content,
        done,
        backgroundClickEnabled: false
    });
};
