module.exports = ({ util, settings, elements }) => () => {
    const $noticeContent = document.createElement('div');
    $noticeContent.innerHTML = 'Do you <mark>really</mark> need more than one active avatar? <a>Learn more</a>';

    const $notice = elements.notice($noticeContent);
    const $learnMore = $noticeContent.querySelector('a');

    $learnMore.addEventListener('click', () => {
        settings.app.setState({ modal: 'multiples' });
    });

    const noticeVisibility = util.visibilityToggle($notice);

    settings.options.onChange('active', ({ active }) => {
        active <= 1 ? noticeVisibility.hide() : noticeVisibility.show();
    }).invoke();

    return $notice;
};
