module.exports = ({ config }) => () => {
    const $titleBar = document.createElement('h1');
    $titleBar.textContent = config.app.name;

    const $author = document.createElement('a');
    $author.className = 'author';
    $author.href = config.author.profile;
    $author.textContent = `by ${config.author.name}`;
    $author.target = '_blank';

    $titleBar.append($author);

    return $titleBar;
};
