module.exports = ({ util, stores, services }) => roleData => {
    const changes = {
        name: roleData.name.trim().toUpperCase(),
        colour: roleData.colour || util.randomColour().hex
    };

    return stores.roles.insert({ ...roleData, ...changes }, roleId => {
        stores.roles.id(roleId).onChange('name', services.roles.propagateRoleName(roleId)).invoke();
    });
};
