module.exports = ({ config, util, tag }) => instanceId => {
    const $layout = document.createElement('div');
    $layout.className = 'layout';

    const rows = util.parseLayout(config.tags.layout).map(row => {
        const $row = document.createElement('div');
        $row.className = 'row';
        const elements = row.map(name => tag.elements[name](instanceId));
        $row.append(...elements);
        return $row;
    });

    $layout.append(...rows);
    return $layout;
};
