module.exports = ({ config, settings, elements }) => () => {
    const $keepSorted = document.createElement('select');

    const options = Object.entries(config.options.sort).map(([key, text]) => {
        const $option = document.createElement('option');
        $option.value = key;
        $option.textContent = text;
        return $option;
    });

    $keepSorted.append(...options);

    $keepSorted.addEventListener('change', () => {
        settings.options.setState({ sort: $keepSorted.value });
    });

    settings.options.onChange('sort', ({ sort }) => {
        $keepSorted.value = sort;
    }).invoke();

    return elements.label('Keep sorted by', $keepSorted);
};
