module.exports = ({ util, elements }) => () => {
    const $tip = document.createElement('div');
    $tip.innerHTML = `
        Save time by renaming files to include name, and optionally, role (separated by +) e.g. matt+dev.jpg<br>
        Once images are provided, <mark>names and roles can be edited directly on each avatar</mark>.<br>
        When done: print, laminate, and decorate your agile board!
    `;

    const $notice = elements.notice($tip);
    const noticeVisibility = util.visibilityToggle($notice);
    noticeVisibility.show();
    return $notice;
};
