module.exports = ({ config, elements, gravatarImporter }) => () => {
    const $fallbacks = document.createElement('div');

    const fallbackOptions = config.gravatar.fallbacks.map(gravatarImporter.fallback);
    $fallbacks.append(...fallbackOptions);

    const labelText =
        'Select a generated image style to use in case profile image is not found.';
    return elements.label(labelText, $fallbacks);
};
