const debounce = require('lodash/debounce');

module.exports = ({ config, util, settings, stores, services, components }) => () => {
    const tagsInstances = {};

    const $tags = document.createElement('div');
    $tags.className = 'tags';

    const insertTag = instanceId => {
        const $tag = components.tag(instanceId);
        tagsInstances[instanceId] = $tag;
        $tags.append($tag);
    };

    const removeTag = instanceId => {
        tagsInstances[instanceId].remove();
        delete tagsInstances[instanceId];
    };

    stores.tagInstances.onInsert(instanceId => {
        insertTag(instanceId);
        delayedSort();
    });

    stores.tagInstances.onBeforeRemove(removeTag);

    const sort = () => {
        services.tags.sortInstances().forEach(instance => {
            $tags.appendChild($tags.removeChild(tagsInstances[instance.id]));
        });
    };

    const delayedSort = debounce(sort, config.debounce.sortTagList);

    settings.options.onChange('sort', sort).invoke();

    stores.tagInstances.onAnyChange({ name: delayedSort });
    stores.tagInstances.onAnyChange({ roleName: delayedSort });



    return $tags;
};
