module.exports = ({ util, stores, components }) => () => {
    const roles = {};

    const $roleListContainer = document.createElement('div');
    $roleListContainer.className = 'role-list-container';

    const $roles = document.createElement('div');
    $roles.className = 'role-list';

    $roles.innerHTML = '<span class="label2">Bulk edit role name and colour:</span><br />';

    $roleListContainer.append($roles);

    const visibilityToggle = util.visibilityToggle($roles);

    const insert = roleId => {
        const $role = components.roleCustomiser(roleId);
        roles[roleId] = $role;
        $roles.appendChild($role);
    };

    stores.roles.ids().forEach(insert);
    stores.roles.onInsert(insert);
    stores.tags.onInsert(visibilityToggle.show); // only show roles when there's a tag

    return $roleListContainer;
};
