module.exports = ({ settings, components, app }) => () => {
    const $container = document.createElement('div');

    $container.append(
        app.modalsContainer(components.modals),
        app.stylesContainer(), 
        app.header(), 
        components.avatarMaker()
    );

    settings.app.onChange('modal', ({ modal }) => {
        $container.className = `app modal-${Boolean(modal)}`;
    }).invoke();

    return $container;
};
