module.exports = ({ util, settings }) => modalMapping => {
    const $modals = document.createElement('div');
    $modals.className = 'modals';

    const done = () => settings.app.setState({ modal: null });

    const modals = Object.entries(modalMapping).reduce((acc, [key, modal]) => {
        return Object.assign(acc, { [key]: modal(done) });
    }, {});

    settings.app.onChange('modal', ({ modal }) => {
        Object.entries(modals).forEach(([key, $modal]) => util.visibilityToggle($modal, modal === key));
    }).invoke();

    $modals.append(...Object.values(modals));
    return $modals;
};
