module.exports = ({ settings, stores, services }) => () => {
    const {
        sortTagsByRoleThenName,
        sortTagsByName,
        sortInstancesByTagThenMode
    } = services.tags;
    const { sort } = settings.options.getState();

    const map = {
        orderAdded: stores.tags.getItems,
        roleThenName: sortTagsByRoleThenName,
        name: sortTagsByName
    };

    const tags = map[sort]();

    return sortInstancesByTagThenMode(tags);
};
