module.exports = ({ config, services, io }) => async email => {
    const { domain } = config.gravatar;
    const emailHash = services.gravatar.hashEmail(email);
    const url = `${domain}/${emailHash}.json`;
    const response = await io.fetch(url);
    if (!response.ok) return null;
    const data = await response.json();
    return data.entry[0];
};
