module.exports = ({ services, components, avatarMaker }) => () => {
    const $avatarMaker = document.createElement('div');
    $avatarMaker.className = 'avatar-maker';

    const insert = async file => {
        const tag = await services.tagDataSources.imageFile(file);
        return services.tags.insertTag(tag);
    };

    const $dropzone = avatarMaker.dropzone(insert);
    $avatarMaker.append($dropzone);

    $dropzone.append(components.controlPanel(), avatarMaker.tagList());

    return $avatarMaker;
};
