// TODO: Refactor

module.exports = ({ services }) => async (inputStr, fallback) => {

    const isEmail = inputStr.includes('@');

    // const splitAt = (x, index) => [x.slice(0, index), x.slice(index)];
    const splitAt = (x, index) => [x.slice(0, index), x.slice(index + 1)]; // +1 to omit +

    const getEmailParts = () => {
        const indexOfAt = inputStr.indexOf('@');
        const lastIndexOfPlus = inputStr.lastIndexOf('+');
        const hasRole = lastIndexOfPlus > indexOfAt;
        
        if (hasRole) {
            const [email, role] = splitAt(inputStr, lastIndexOfPlus);
            return [email, role];
        } 
        const email = inputStr;
        const role = '';
        return [email, role];
        
    };

    const [email, role] = isEmail ? getEmailParts() : inputStr.split('+');

    console.log([email, role]);

    const [profile, imageBlob] = await Promise.all([
        services.gravatar.fetchProfile(email),
        services.images.fromGravatar(email, fallback)
    ]);

    const name = profile
        ? extractName.fromProfile(profile)
        : extractName.fromEmail(email);

    const expression = [name, role].join('+');

    return { expression, imageBlob };
};

const extractName = {
    fromProfile: profile => {
        return profile.name.givenName || profile.displayName;
    },
    fromEmail: email => {
        return email.split('@')[0].split('+')[0]; // before the first plus
    }
};

